.latest-releases{
	margin-bottom: 48px;
	@media (max-width:$md2+px){
		margin-bottom: 46px;
	}
	&__row{
		@media (min-width:$md2+px){
			 display: flex; 
		}
	}
	&__column{
		flex: 1 1 auto;
		&:first-child{
			flex: 0 0 483/1130 * 100%;
		}
	}

	&__title{
		margin-bottom: 82px;
		@media (max-width:$md2+px){
			margin-bottom: 22px;
		}
	}
	&__spollers{
		max-width: 263px;
		margin-bottom: 31px;
		@media (max-width:$md2+px){
			display: none;
		}
	}
}


.mediathek-block{
	margin-bottom: 50px;
	position: relative;

	&__container{
		@media (min-width:$md4+px){
			justify-content: flex-end;
		}
		display: flex;
		justify-content: center;
	}
	&__content{
		flex: 0 0 499/1130 * 100%;
		padding: 70px 0;
		@media (max-width:$md2+px){
			flex: 0 1 435px;
			padding: 134px 0;
		}
		@media (max-width:$md5+px){
			 flex: 0 0 289px;
		}
	}
	&__bg{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
	&__text{
		font-style: italic;
		margin-bottom: 38px;
		font-size: 36px;
		font-weight: 500;
		color: $color__black-dark;
		letter-spacing: 0.23px;
		line-height: 42px;
		text-align: center;
		@media (max-width:$md2+px){
			margin-bottom: 29px;
		}
		@media (max-width:$md5+px){
			font-size: 24px;
			font-weight: 500;
			letter-spacing: 0.16px;
			line-height: 28px;
		}
	}
	&__button{
		max-width: 290px;
		margin: 0 auto;
		@media (max-width:$md2+px){
			max-width: 280px;
		}
	}
}
